/**************************/
:root {
    --white: #fff;
    --black: #000;
    --bg: #f8f8f8;
    --grey: #999;
    --dark: #1a1a1a;
    --light: #e6e6e6;
    --wrapper: 1000px;
    --blue: #00b0ff;
}

/*************************/

.Dialogs_chatBlocks__BD3ao{
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-areas:
            "fir sec";
}

.Dialogs_chatFirst__1DF5G{
    font-size: 14px;
    line-height: 22px;
    color: #000;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 600;
    grid-area: fir;
}
.Dialogs_chatFirst__1DF5G>div>a{
    color: #000;
    text-decoration: none;
}
.Dialogs_chatFirst__1DF5G>div{
    position: relative;
    width: 100%;
    padding: 22px 0% 35px;
    cursor: pointer;
    background-color: #fff;
    background-color: var(--white);
}
.Dialogs_chatSecond__4iTXz{
    grid-area: sec;
}
textarea {
    float: left;
    width: 188px;
    height: 42px;
    padding: 0 15px;
    border: 1px solid #e6e6e6;
    border: 1px solid var(--light);
    background-color: #eceff1;
    border-radius: 21px;
    outline: none;
}
.Dialogs_send__PFoVi:before {
    display: inline-block;
    float: left;
    width: 20px;
    height: 42px;
    margin-left: 11px;
    content: '';
    background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/382994/send.png");
    background-repeat: no-repeat;
    background-position: center;
}
body {
    padding: 0;
    margin: 0;
    background: -webkit-linear-gradient(-45deg, #183850 0, #183850 25%, #192c46 50%, #22254c 75%, #22254c 100%);
    background-repeat: no-repeat;
    background-attachment: fixed;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: rgba(25, 147, 147, 0.1);
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: rgba(25, 147, 147, 0.2);
}

.Dialogs_chatThread__1FBb7 {
    margin: 24px auto 0 auto;
    padding: 0 20px 0 0;
    list-style: none;
    overflow-y: scroll;
    overflow-x: hidden;
}

.Dialogs_chatThread__1FBb7 li {
    position: relative;
    clear: both;
    display: inline-block;
    padding: 16px 40px 16px 20px;
    margin: 0 0 20px 0;
    font: 16px/20px 'Noto Sans', sans-serif;
    border-radius: 10px;
    background-color: #000;
}

/* Chat - Avatar */
.Dialogs_chatThread__1FBb7 li:before {
    position: absolute;
    top: 0;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    content: '';
}

/* Chat - Speech Bubble Arrow */
.Dialogs_chatThread__1FBb7 li:after {
    position: absolute;
    top: 15px;
    content: '';
    width: 0;
    height: 0;
    border-top: 15px solid #000;
}

.Dialogs_chatThread__1FBb7 li:nth-child(odd) {
    animation: Dialogs_show-chat-odd__37s1o 0.15s 1 ease-in;
    -moz-animation: Dialogs_show-chat-odd__37s1o 0.15s 1 ease-in;
    -webkit-animation: Dialogs_show-chat-odd__37s1o 0.15s 1 ease-in;
    float: right;
    margin-right: 80px;
    color: #0AD5C1;
}



.Dialogs_chatThread__1FBb7 li:nth-child(odd):after {
    border-right: 15px solid transparent;
    right: -15px;
}

.Dialogs_chatThread__1FBb7 li:nth-child(even) {
    animation: Dialogs_show-chat-even__2KCsM 0.15s 1 ease-in;
    -moz-animation: Dialogs_show-chat-even__2KCsM 0.15s 1 ease-in;
    -webkit-animation: Dialogs_show-chat-even__2KCsM 0.15s 1 ease-in;
    float: left;
    margin-left: 80px;
    color: #0EC879;
}

.Dialogs_chatThread__1FBb7 li:nth-child(even):before {
    left: -80px;
}

.Dialogs_chatThread__1FBb7 li:nth-child(even):after {
    border-left: 15px solid transparent;
    left: -15px;
}

.Dialogs_chat-window__TM8zu {
    position: fixed;
    bottom: 18px;
}

.Dialogs_chat-window-message__gM9gc {
    width: 100%;
    height: 48px;
    font: 32px/48px 'Noto Sans', sans-serif;
    background: none;
    color: #0AD5C1;
    border: 0;
    border-bottom: 1px solid rgba(25, 147, 147, 0.2);
    outline: none;
}

/* Small screens */
@media all and (max-width: 767px) {
    .Dialogs_chatThread__1FBb7 {
        width: 90%;
        height: 260px;
    }

    .Dialogs_chat-window__TM8zu {
        left: 5%;
        width: 90%;
    }
}
/* Medium and large screens */
@media all and (min-width: 768px) {
    .Dialogs_chatThread__1FBb7 {
        margin-left: 5%;
        height: 100%;
    }

    .Dialogs_chat-window__TM8zu {
        left: 25%;
        width: 50%;
    }
}
@keyframes Dialogs_show-chat-even__2KCsM {
    0% {
        margin-left: -480px;
    }

    100% {
        margin-left: 0;
    }
}
@-webkit-keyframes Dialogs_show-chat-even__2KCsM {
    0% {
        margin-left: -480px;
    }

    100% {
        margin-left: 0;
    }
}
@keyframes Dialogs_show-chat-odd__37s1o {
    0% {
        margin-right: -480px;
    }

    100% {
        margin-right: 0;
    }
}
@-webkit-keyframes Dialogs_show-chat-odd__37s1o {
    0% {
        margin-right: -480px;
    }

    100% {
        margin-right: 0;
    }
}
